	baseUrl="http://localhost:3000"

export default function(url,data={},method="GET"){
	return new Promise((resolve,reject)=>{
		uni.request({
			// url:"http://localhost:3000/getIndexData",
			// url:"/api/getIndexData",
			url:baseUrl + url,
			data,
			method,
			header:{
				token:uni.getStorageSync('token')
			},
			success:(res)=>{
				resolve(res.data)
			}
		})
	})
}